import React, { useState } from 'react'

export default function FileChooseActLabelFuncSate({ onFileUpload, getFileUploadButtonLabel }) {
  const [file, setFile] = useState(null);
  const onDragOver = event => {
    event.stopPropagation();
    event.preventDefault();
    event.dataTransfer.dropEffect = 'copy';
  };

  const onDrop = async event => {
    event.stopPropagation();
    event.preventDefault();
    const file = event.dataTransfer.files[0];
    if (!file)
      throw new Error(`Only files can be dropped here`);
    setFile(file)
  }

  return (
    <>
      <input
        id="files"
        type="file"
        style={{ display: 'none' }}
        accept=".json"
        onChange={e => setFile(e?.target?.files[0])}
      />
      <label
        //className="dropzone"
        onDragOver={onDragOver}
        onDrop={onDrop}
        htmlFor="files">
        {file
          ? <><p>Выбран файл: {file.name}. </p><p>Для выбора другого файла,</p></>
          : <><p>Файл не выбран. </p><p>Для выбора файла,</p></>}
        <p>нажмите, или перетащите файл сюда</p>
      </label>
      <br />
      <button
        style={file ?? { display: 'none' }}
        onClick={() => onFileUpload?.(file)}>
        {getFileUploadButtonLabel?.(file)}
      </button>
    </>
  )
}