import React from "react";
import './IDBIE1.css';
import Dexie from 'dexie';
import { importInto } from 'dexie-export-import';
import FileChooseLabelActFuncState from '../../FileChouseLabelAct/FuncState/FileChooseLabelActFuncState';
import db from './db';
import progressCallback from './progressCallback'

export default function ImportFile({ onAfterImport }) {

    const importFile = async file => {
        try {
            await Dexie.delete('exportSample');
            await db.open();
            await importInto(db, file, { progressCallback });
            await onAfterImport(file);
        } catch (error) {
            console.error('' + error);
        }
    }
    return < >
        <FileChooseLabelActFuncState
            onFileUpload={importFile}
            getFileUploadButtonLabel={file => `Загрузить файл ${file?.name} `} />
    </>;
}