import React, { useRef } from "react";
import './IDBIE1.css';
import ImportFile from './ImportFile';
import ExportFile from './ExportFile';
import DatabaseInfo from './DatabaseInfo';

export default function IDBIE1() {
    const databaseInfo = useRef();
    return < >
        <div className="column">
            <h3>Export</h3>
            <ExportFile />
        </div>

        <div className="column">
            <h3>Import</h3>
            <ImportFile onAfterImport={() => databaseInfo.current.updateTablesAsync()}
            />
        </div>

        <div className="column">
            <h3>Database Info</h3>
            <DatabaseInfo ref={databaseInfo} />
        </div>
    </>;
}
