import React from "react";
import db from './db.js';

export default class DatabaseInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = { tables: [] };
        this.updateTablesAsync();
    }

    updateTablesAsync = async () => this.setState({
        tables: await Promise.all(db.tables.map(async table => (
            <tr key={table.name}>
                <th>Table: {table.name}</th>
                <td>Primary Key:{table.schema.primKey.src}</td>
                <td>Row count: {await table.count()}</td>
            </tr>)))
    })

    render = () => < >
        <table border="1">
            <thead></thead>
            <tbody>
                <tr>
                    <th>Database Name</th>
                    <td colSpan="2">{db.name}</td>
                </tr>
                {this.state.tables}
            </tbody>
        </table>
    </>;
}